@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&family=Viga&display=swap");
:root {
  --transparent-white-color: rgba(255, 255, 255, 0.7);
  --white-color: #fff; }

.container {
  padding: 0 44px; }
  @media screen and (min-width: 768px) {
    .container {
      padding: 0 88px; } }

.design {
  height: 100vh;
  display: flex;
  align-items: center;
  background-image: url("../images/bg-1.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover; }

.motion {
  height: 100vh;
  display: flex;
  align-items: center;
  background-image: url("../images/bg-2.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover; }

.engine {
  height: 100vh;
  display: flex;
  align-items: center;
  background-image: url("../images/bg-3.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover; }

.info {
  font-family: "Roboto", sans-serif;
  width: 70vw; }
  @media screen and (min-width: 768px) {
    .info {
      width: 527px; } }

.subtitle {
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  margin-left: 5px;
  color: var(--white-color);
  margin-bottom: 1px;
  text-transform: uppercase; }
  @media screen and (min-width: 768px) {
    .subtitle {
      margin-left: 10px; } }
  @media screen and (min-width: 768px) {
    .subtitle {
      font-weight: 700;
      font-size: 28px;
      line-height: 32px; } }

.title {
  font-family: "Viga", sans-serif;
  font-weight: 400;
  font-size: 60px;
  line-height: 64px;
  color: var(--white-color);
  margin-bottom: 65px;
  padding: 0;
  text-transform: uppercase; }
  @media screen and (min-width: 768px) {
    .title {
      font-weight: 400;
      font-size: 120px;
      line-height: 128px; } }

.description {
  font-weight: 400;
  font-size: 10px;
  line-height: 15px;
  margin-left: 5px;
  color: var(--transparent-white-color);
  margin-bottom: 39px; }
  @media screen and (min-width: 768px) {
    .description {
      margin-left: 10px; } }
  @media screen and (min-width: 768px) {
    .description {
      font-weight: 400;
      font-size: 20px;
      line-height: 30px; } }

.more {
  margin-left: 5px;
  display: flex;
  align-items: center; }
  @media screen and (min-width: 768px) {
    .more {
      margin-left: 10px; } }
  .more__link {
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
    position: relative;
    color: var(--white-color);
    text-decoration: none; }
    @media screen and (min-width: 768px) {
      .more__link {
        font-weight: 400;
        font-size: 24px;
        line-height: 30px; } }
    .more__link:after {
      position: absolute;
      content: "";
      height: 1px;
      bottom: -2px;
      left: 0;
      right: 0;
      width: 90%;
      background: var(--white-color); }
  .more__icon {
    display: inline-block;
    height: 5px;
    width: 5px;
    margin-right: 8px; }
    @media screen and (min-width: 768px) {
      .more__icon {
        height: 10px;
        width: 10px; } }

.arrow {
  width: 100vw;
  display: flex;
  justify-content: space-between;
  padding-bottom: 30px; }
  @media screen and (min-width: 768px) {
    .arrow {
      padding-bottom: 60px; } }
  .arrow__btn {
    background-color: transparent;
    border: none;
    outline: none;
    cursor: pointer; }

.glide {
  position: relative;
  width: 100%;
  box-sizing: border-box; }
  .glide * {
    box-sizing: inherit; }
  .glide__track {
    overflow: hidden; }
  .glide__slides {
    position: relative;
    width: 100%;
    list-style: none;
    backface-visibility: hidden;
    transform-style: preserve-3d;
    touch-action: pan-Y;
    overflow: hidden;
    margin: 0;
    padding: 0;
    white-space: nowrap;
    display: flex;
    flex-wrap: nowrap;
    will-change: transform; }
    .glide__slides--dragging {
      user-select: none; }
  .glide__slide {
    width: 100%;
    height: 100%;
    flex-shrink: 0;
    white-space: normal;
    user-select: none;
    -webkit-touch-callout: none;
    -webkit-tap-highlight-color: transparent; }
    .glide__slide a {
      user-select: none;
      -webkit-user-drag: none;
      -moz-user-select: none;
      -ms-user-select: none; }
  .glide__arrows {
    -webkit-touch-callout: none;
    user-select: none; }
  .glide__bullets {
    -webkit-touch-callout: none;
    user-select: none; }
  .glide--rtl {
    direction: rtl; }

.glide {
  height: 100vh; }
  .glide__arrows {
    position: absolute;
    bottom: 0; }
  .glide__slides {
    height: 100vh; }

